/**@jsx jsx*/
import {jsx} from '@emotion/core'
import React, {useState, useEffect} from 'react'
import {logout} from "../../helpers";
import {Redirect} from "react-router-dom";
import {LOGIN_PAGE, LOGOUT_ERROR_PAGE} from "../../constants";



const Logout = ({clearFunction}) => {
  const REDIRECT_TIMEOUT  =2000
  const [loggedOut, setLoggedOut] = useState(false)
  const [error, setError] = useState(false)
  const [redirect, setRedirect] = useState(false)


  useEffect(() => {
    logout().then(
      (res) => {
        if (res) {
          clearFunction()
          setLoggedOut(true)
          setTimeout(() => setRedirect(true), REDIRECT_TIMEOUT)
        } else {
          setError(true)
        }
      }
    )
  }, [])

  return loggedOut
    ? redirect
      ? <Redirect to={LOGIN_PAGE}/>
      : <h1 className='text-center'>You are logged out</h1>
    : error
      ? <Redirect to={LOGOUT_ERROR_PAGE}/>
      : <h1 className='text-center'>Logout...</h1>
}

export default Logout